import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-premier-checking',
  templateUrl: './premier-checking.page.html',
  styleUrls: ['./premier-checking.page.scss'],
})
export class PremierCheckingPage implements OnInit {

  myObject:string;

  constructor(public navCtrl: NavController,public activatedRoute : ActivatedRoute) { 
    this.activatedRoute.queryParams.subscribe((res)=>{
      this.myObject = (JSON.stringify(res)); 
    });

  }

  ngOnInit() {
  }

  goToChatWindow() {
    this.navCtrl.navigateForward("chat-page");
  }

  goBack() {
  if(this.myObject.includes("1")) {
    this.navCtrl.navigateForward("social-security-number");
  }else
    this.navCtrl.navigateForward("country-of-citizenship");
  }



}
